.login-max {
  width: 1920px;
  height: 100%;
  background-image: url('../images/login/bg.png');
  position: relative;
}
.login-box {
  width: 456px;
  height: 600px;
  background-color: #ffffff;
  box-shadow: 0px 3px 10px 0px rgba(76, 132, 255, 0.2);
  border-radius: 10px;
  position: absolute;
  right: 300px;
  top: 140px;
}
.login-box .login {
  width: 100%;
  height: 100%;
  position: relative;
}
.login-img {
  width: 184px;
  height: 52px;
}
.login-img img {
  position: absolute;
  top: 74px;
  left: 136px;
}
.login-user {
  margin-top: 130px;
  margin-left: 68px;
  position: relative;
}
.login-pass {
  margin-left: 68px;
  margin-top: 28px;
  position: relative;
}
.login-user input,
.login-pass input {
  width: 320px;
  height: 48px;
  background-color: #f0f4f9;
  border-radius: 4px;
  border: solid 1px #dee7ef;
}
.login-user input::-webkit-input-placeholder,
.login-pass input::-webkit-input-placeholder {
  font-family: Source Han Sans CN;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: 0px;
  color: #9aa2b5;
  position: relative;
  left: 48px;
}
.login-user img,
.login-pass img {
  position: absolute;
  top: 15px;
  left: 13px;
}
.login-login {
  margin-top: 51px;
  margin-left: 68px;
}
.login-btn {
  width: 320px;
  height: 48px;
  outline: none;
  border: none;
  background-color: #77a726;
  border-radius: 4px;
  font-family: Source Han Sans CN;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 31px;
  letter-spacing: 0px;
  color: #ffffff;
}
.login-test {
  margin-left: 200px;
  margin-top: 20px;
  font-family: Source Han Sans CN;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: 0px;
  color: #4e5666;
}
.login-compute {
  font-family: Source Han Sans CN;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: 0px;
  color: #9aa2b5;
  position: absolute;
  bottom: 200px;
  left: 114px;
}
.login-logo-img img {
  position: absolute;
  right: 20px;
  top: -172px;
}
